export default function (params, type) {
	switch (type) {
		case 'challengeowner': {
			return {
				// ownerIdx: params.ownerIdx,
				memberIdx: params.memberIdx,
				ownerCat: params.ownerCat,
				company: params.company,
				email: params.email,
				contact: params.contact,
				socialMediaCode: params.socialMediaCode,
				socialMediaId: params.socialMediaId,
				// "registMemberIdx": 0,
				// "registDate": "2021-07-12T11:42:54.308Z",
				// "modifyMemberIdx": 0,
				// "modifyDate": "2021-07-12T11:42:54.308Z"
			};
		}
		case '': {
			return {
				missions: [
					// 
					{
						videos: params.videos.map((el) => {
							return {
								// "challengeIdx": 0,
								seq: 1, //seq  1
								platform: el.platform, //
								platformRequired: 1,
							};
						}),
						// {
						// 	// "challengeIdx": 0,
						// 	seq: 1, //seq  1
						// 	platform: 'YU', //
						// 	platformRequired: params.platformRequired,
						// },

						seq: 1, // 1
						missionDesc: 'string',
						datePub: params.datePub, // 
						dateBegin: params.dateBegin, // 
						dateFin: params.dateFin, //  
						shareRequired: params.shareRequired, //   2
						filmRequired: params.filmRequired, //    2
						emailRequired: params.emailRequired, //     
						contactRequired: params.contactRequired, //    
					},
				],

				ownerName: params.ownerName, //userInfo. name
				memberIdx: params.memberIdx, // userInfo . memberidx
				rewards:
					//   {
					//     "seq": 1,
					//     "cat": 0,
					//     "qty": 0,
					//     "pts": 0,
					//     "currency": "string",
					//   }
					params.rewards.map((el) => {
						return {
							seq: 1,
							cat: el.cat,
							qty: el.qty,
							pts: el.pts,
							currency: el.currency,
						};
					}),

				// "challengerCount": 0, //?
				// "commentCount": 0,
				// "shareCount": 0,
				// "challengeIdx": 0, //?
				title: params.title, // 
				// "subtitle": "string", //preview   
				ownerIdx: params.ownerIdx, //userinfo.member idx
				companyA: params.companyA, //
				companyB: params.companyB, //
				url: params.url, // URL
				challengeDesc: params.challengeDesc, // 
				// "meetCode": 0, //?
				challengeTargetCode: 1, // 1
				// datePub: params.datePub, // 
				// "logo": "string", //?
				mainImage: params.mainImage, //
				fileRef: params.fileRef, //
				// "promoting": 0, //?
				commentAllowed: params.commentAllowed ? 1 : 0, //
				charge: params.charge, //
				chargeShown: params.chargeShown ? 1 : 0,
				chargeContact: params.chargeContact,
				chargeContactShown: params.chargeContactShown ? 1 : 0,
				chargeeMail: params.chargeeMail,
				chargeeMailShown: params.chargeeMailShown ? 1 : 0,
				// "registMemberIdx": params.registMemberIdx,//userinfo.member idx // 4 .
				// "registDate": params.registDate, // 
				// "modifyMemberIdx": params.registMemberIdx,//userinfo.member idx
				// "modifyDate": params.registDate // 
			};
		}
	}
}
